.button_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    .button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: auto;

        padding: 7px 20px;
        user-select: none;

        font-size: 20px;

        border: none;
        outline: none;

        border-radius: 5px;

        cursor: default;

        &:active &:disabled {
        }
        &:enabled {
            cursor: pointer;
            &:hover {
            }
            &:active {
                // transition: 0.4s;
                // transform: scale(0.3);
            }
        }
    }
}
