.input_main_wrapper {
    width: 500px;
    padding-top: 10px;
    @media screen and (max-width: 567px) {
        & {
            width: 90vw;
        }
    }

    .input_field {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        .label {
            position: absolute;
            top: 50%;
            left: 5px;
            transform: translateY(-50%);
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 14px;
            transition: all 200ms;
            user-select: none;
            cursor: text;

            font-size: 600;

            border-radius: 4px;
        }

        .input:focus + .label, // TODO
        .label_active {
            letter-spacing: 0.04em;
            top: 0;
            left: 5px;
            padding: 1px 7px;
        }

        .input {
            width: 100%;
            height: 50px;
            border-radius: 5px;
            border: none;
            outline: none;
            box-sizing: border-box;
            padding-left: 5px;

            transition: 1s;

            &::placeholder {
                color: transparent;
                background: transparent;
                font-size: 0px;
            }

            &:hover {
            }
            &:active {
                border: 20px;
            }
            &:focus {
                border: 20px;
            }

            &.has_errors {
            }
            &.correct {
            }

            &:-webkit-autofill,
            &:-webkit-autofill:hover,
            &:-webkit-autofill:focus,
            &:-internal-autofill-selected {
                -webkit-appearance: unset !important;
                background-color: red !important;
                color: orange !important;
                -webkit-box-shadow: 0 0 0 30px transparent green !important;
            }

            // TODO
        }
    }
    .red_message {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding: 5px 10px;
        font-size: 16px;
        font-weight: 600;
    }
}
