.errorMessageWrapper {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0 15px;

    .errorMessage {
        box-sizing: border-box;
        padding: 5px 10px;
        margin: 15px 5px;
    }
}

.form_wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;

    .form {
        padding: 0 25px;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        transition: 1s;

        padding-bottom: 10px;

        .page_name {
            font-size: 25px;
            padding: 10px 0;
            text-align: center;
            text-transform: capitalize;
        }

        .lw {
            text-align: center;
            padding: 3px 0;

            .ttl {
                font-size: 16px;
            }

            .link {
                font-size: 18px;
                // color: red;
            }
        }
    }
}

.help_link {
    font-size: 18px;
    display: inline-flex;
    padding-left: 5px;

    // color: rgba(0, 136, 233, 0.614);

    &:hover {
        // color: rgba(0, 136, 233, 0.737);
    }
    &:active {
        // color: rgba(0, 77, 133, 0.792);
    }
}
.help_wrapper {
    margin-top: 50vh;
}
.reCaptcha_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0 20px 0;
}
.captcha_loading {
    display: inline-flex;
    justify-content: center;
    margin-bottom: 10px;

    .captcha_loading_message {
        font-size: 17px;
        text-transform: capitalize;
        cursor: not-allowed;
        &:after {
            content: "...";
        }
    }
}

.cancelBtnWrapper {
    margin: 7px 0;

    display: flex;
    justify-content: center;

    .cancelBtn {
        background: red;
        padding: 10px;
        border-radius: 5px;

        cursor: pointer;
    }
}
